/* Label Text */
.table a {
  color: #147ccc;
  text-decoration: none; }
  .table a:hover, .table a:focus, .table a:active {
    text-decoration: none; }

.table > thead > tr > th {
  vertical-align: middle;
  text-align: center;
  background: #4C6375;
  border: 1px solid #fff;
  border-top: 1px solid #d6d6d6;
  color: #fff;
  font-weight: 500;
  padding: 4px 5px;
  border-radius: 0; }
  .table > thead > tr > th:first-child {
    border-left-color: #d6d6d6; }
  .table > thead > tr > th:last-child {
    border-right-color: #d6d6d6; }

.table > tbody tr td {
  border: 1px solid #d6d6d6;
  color: #2c2c2c;
  padding: 4px 5px;
  background: #fff;
  vertical-align: middle; }

.table > tbody tr:hover td {
  background: #fcf4df; }

.table > tbody tr.total td {
  background: #B8C3C9; }

.table > tbody tr.highlight td {
  background: #f7dbb6;
  font-weight: bold; }

.table-container {
  overflow-x: auto; }

.header-content,
.body-content,
.footer-content {
  border: solid 1px #d6d6d6; }
  .header-content .table,
  .body-content .table,
  .footer-content .table {
    border-right: 1px solid #d6d6d6; }
    .header-content .table td:first-child,
    .body-content .table td:first-child,
    .footer-content .table td:first-child {
      border-left: none; }

.table {
  table-layout: fixed; }
  .table th, .table td {
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    min-width: 1px; }

.header-content,
.body-content,
.footer-content,
.table-wrapper {
  position: relative; }

.header-content,
.footer-content {
  padding-right: 17px; }

.first-row {
  height: auto; }
  .first-row td {
    height: 0;
    border: none !important;
    padding: 0 !important;
    line-height: 0; }

.body-content {
  overflow-y: auto;
  overflow-x: auto;
  border-top: none; }
  .body-content.hasScroll {
    padding-right: 0; }
  .body-content tr:last-child td {
    border-bottom: none; }

.header-content {
  border-bottom: none; }
  .header-content .table > thead > tr > th {
    border: none;
    border-left: 1px solid #fff; }
    .header-content .table > thead > tr > th:first-child {
      border-left: none; }
  .header-content .table > thead > tr:nth-child(n + 3) {
    border-top: 1px solid #fff; }

.footer-content {
  border-top: none; }
  .footer-content .table {
    font-weight: bold; }
    .footer-content .table .fixed-table-total td {
      border-bottom: none; }
    .footer-content .table .fixed-table-grant-total td {
      background: #b8c3c9; }
